<script>
import constants from "../../util/constants";

export default {
  name: "login",
  data() {
    return {
      constants,
      username: "",
      password: "",
      is_loging: false
    };
  },
  methods: {
    getUserLocalisation(onDone) {
      // if(onDone) {
      //   onDone()
      //   return
      // }
      this.action_message = "Localisation en cours";
      this.is_loging = true;

      let onSuccess = (position) => {
        let latitude = position.coords.latitude
        let longitude = position.coords.longitude
        //alert("Latitude : " + latitude +  ".\n Longitude : " + longitude)
        this.is_loging = false;
        if(onDone) {
          onDone({latitude, longitude})
        }
      }
      let onError = (error) => {
        this.is_loging = false;
        let message = "";
        if(error.code == error.PERMISSION_DENIED) {
          message = "Autorisation refusée"
        } else if(error.code == error.POSITION_UNAVAILABLE) {
          message = "Les informations de localisation sont indisponibles"
        } else if(error.code == error.TIMEOUT) {
          message = "La demande de localisation a expirée"
        } else if(error.code == error.UNKNOWN_ERROR) {
          message = "Une erreur inconnue s'est produite"
        }
        if(message!="") {
          // alert(message)
          this.$swal({
            type: "error",
            title: "Erreur",
            text: message,
            //footer: '<a href>Why do I have this issue?</a>'
          });
        }
      }
      if(navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(onSuccess, onError)
      } else {
        alert("La géolocalisation est désactivée. Veuillez la réactiver")
        this.$swal({
          type: "error",
          title: "Géolocalisation désactivée",
          text: "La géolocalisation est désactivée. Veuillez la réactiver",
          //footer: '<a href>Why do I have this issue?</a>'
        });
        return ;
      }
    }, 
    doAction() {
      this.getUserLocalisation((position) => this.doLogin(position))
    },
    async doLogin(position) {
      sessionStorage.clear();

      
      // en attendant

      /*
      this.$store.commit("change_connected", {
          ...{
            nom : "Lelisa",
            prenom : "app",
            username : "",
            nom_utilisateur : "",
            nomUtilisateur : "",
            token : "HELLo",
            est_admin  : "1"
          },
          is_connected: true,
        }); 
        this.$snotify.success("Connexion effectuée avec succès", {
          timeout: 1500,
          showProgressBar: false,
          closeOnClick: false,
          pauseOnHover: true,
          
        });
        this.$router.push( "/admin" );
        if(position) position = {...position}
      return 
      /*  */
        
      
      this.is_loging = true;
      let url = constants.api_url + "/auth";
      // let headers = new Headers();
      // headers.append("Content-type", "application/x-www-form-urlencoded");
      let response = await fetch(url, { 
        headers  : {
            "Content-Type" : "application/json"
          },
        // body: 
        //   "login=" +
        //   encodeURIComponent(this.username) +
        //   "&password=" +
        //   encodeURIComponent(this.password),
         body : JSON.stringify({
            email : this.username,
            password : this.password, 
            user_lat : position.latitude,
            user_long : position.longitude, 
          }),
        // mode: "cors",
        method: "post",
        // credentials: "include",
      });
      let data = await response.json();
      this.is_loging = false;
 
      if (data.success != true) {
        // this.$swal({
        //   type: "warning",
        //   title: "Données incorrectes",
        //   text: "Aucun compte n'a été trouvé avec les informations que vous avez renseignée. Vérifiez votre nom d'utilisateur et votre mot de passe.",
        //   // footer: "<a href>Why do I have this issue?</a>",
        // });

        // alert("Erreur : " + data.message)

        this.$snotify.warning("Erreur : " + data.message, {
          // timeout: 1500,
          showProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
         });
      }  else if (data.success == true) {
        this.$store.commit("change_connected", {
          ...data.user,
          nom_utilisateur : data.user.email,
          nomUtilisateur : data.user.email,
          id_utilisateur : data.user.id,
          is_connected: true,
          est_admin : 1
        }); 
        this.$snotify.success("Connexion effectuée avec succès", {
          timeout: 1500,
          showProgressBar: true,
          closeOnClick: true,
          pauseOnHover: true,
        });

        this.$router.push( "/admin" );

        // if (this.$route.query.next) {
        //   this.$router.push(this.$route.query.next);
        // } else {
        //   this.$router.push({ name: "/admin" });
        // }
      }
      /*  */
    },
    
  },
};
</script>

<template>
  <section class="login">
    <div class="container-scroller">
      <div class="container-fluid page-body-wrapper full-page-wrapper">
        <div class="content-wrapper d-flex align-items-center auth">
          <div class="row w-100 flex-grow">
            <div class="col-lg-5 col-md-6 mx-auto">
              
              <div class="auth-form-light text-left p-5">
                <!-- <div class="brand-logo">
                  <img src="../../assets/images/logo.svg" />
                </div> -->

                <div class="mb-5 text-center d-none">
                  <h3>Connexion</h3>
                </div>
                <h3>Connexion</h3>
                <!-- <h6 class="font-weight-light">Accédez à votre compte</h6> -->
                <form class="pt-3 mt-4">
                    <fieldset :disabled="is_loging">   
                      <div class="form-group">
                        <input
                          type="email"
                          class="form-control form-control-lg"
                          id="exampleInputEmail1"
                          placeholder="Nom d'utilisateur"
                          v-model="username"
                        />
                      </div>
                      <div class="form-group">
                        <input
                          type="password"
                          class="form-control form-control-lg"
                          id="exampleInputPassword1"
                          placeholder="Mot de passe"
                          v-model="password"
                        />
                      </div>
                      <!-- <div
                        class="my-2 d-flex justify-content-between align-items-center"
                      >
                        <div class="form-check"></div>
                        <a href="javascript:void(0);" class="auth-link text-black"
                          >Mot de passe oublié ?</a
                        >
                      </div> -->
                      <div class="mb-2">
                        <button
                          type="button"
                          class="btn btn-block btn-facebook auth-form-btn"
                          @click="doAction"
                          :disabled="username == '' || password == '' || is_loging"
                        >
                          Se connecter
                        </button>
                      </div>
                    </fieldset>
                  <div class="text-center mt-4 font-weight-light">
                    Vous n'avez pas de compte ?
                    <router-link to="/signup" class="text-primary"
                      >Inscrivez-vous</router-link
                    >
                  </div>
                </form>
              </div>

              <div
                class="text-center mt-5 navbar-brand-wrapper flex-column d-flex align-items-center justify-content-center"
              >
                 <!-- <img
                    src="@/assets/logo-site.png"
                    style="width: 80px; height: auto"
                    alt="logo"
                  />   -->
                <p class="mt-3 text-3">Lelisa App</p>
              </div>

            </div>
          </div>
        </div>
        <!-- content-wrapper ends -->
      </div>
      <!-- page-body-wrapper ends -->
    </div>
  </section>
</template>


